<template>
	<div>
		<HeaderComponent :view-title="bindPhoneTitle"></HeaderComponent>
		<form id="main-form">
			<section class="inputPNum">
				<label>手机号</label>
				<input type="text" name="phoneNumber" placeholder="请输入手机号">
			</section>
			<section class="getCode">
				<label>验证码</label>
				<input type="text" name="checkCode" placeholder="请输入短信验证码">
				<button>获取验证码</button>
			</section>
		</form>
		<div class="sureBtn">确	定</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		bindPhoneTitle:Object,
		data(){
			return{
				bindPhoneTitle:{
					name:'绑定手机号',
					showCode:1
				}
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	#main-form{
		width:100%;
		background:#fff;
		padding:0 0.5rem;
		margin-top:2.2rem;
		.inputPNum,.getCode{
			width:100%;
			height:2.5rem;
			line-height: 2.5rem;
			border-bottom:1px solid #D9D9D9;
			@include sc(0.7rem,#6F6F6F);s
			label{
				font-size:0.7rem;
			}
			input{
				width:7rem;
				height:1rem;
				line-height: 1rem;
				font-size:0.7rem;
				margin-left:0.4rem;
				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  @include sc(0.7rem,#A1A1A1);
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  @include sc(0.7rem,#A1A1A1);
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  @include sc(0.7rem,#A1A1A1);
				}
				&:-moz-placeholder { /* Firefox 18- */
				  @include sc(0.7rem,#A1A1A1);
				}

			}
			button{
				width:4.4rem;
				height:1.3rem;
				background:#F35353;
				@include sc(0.7rem,#FBDFDF);
			}
		}

	}
	.sureBtn{
		width:90%;
		height:2rem;
		line-height: 2rem;
		text-align:center;
		margin:0 auto;
		margin-top:2rem;
		background:#F35353;
		@include sc(0.9rem,#FBDFDF);
		@include borderRadius(0.2rem);
	}
</style>


